.social {
    display: flex;
    justify-content: center;

    margin-bottom: 1.5rem;

    @include breakpoint($tablet-bp) {
        display: none;
    }
}

.social-item {
    height: 25px;
    width: 25px;
    margin: 0 .75rem;
}

.social-link:hover {
    img {
        transform: scale(1.1);
    }
}